.asColorPicker-wrap {
  position: relative;
  display: inline-block;
}

.asColorPicker_hideInput {
  display: none;
}

.asColorPicker_hideInput .asColorPicker-clear {
  display: none;
}

.asColorPicker-dropdown {
  position: absolute;
  z-index: 9999; 
  display: none;
  -webkit-user-select: none;
