}

.asColorPicker-clear {
  position: absolute;
  top: 0;
  right: 26px;
  display: none;
  color: #777;
  text-decoration: none;
}
.asColorPicker-clear:after {
  content: "x";
}

.asColorPicker-wrap:hover .asColorPicker-clear {
  display: inline-block;
}

.asColorPicker-preview {
  float: left;
